<template>
  <router-view></router-view>
</template>

<script>
  import { Toast } from 'vant'
export default {
  name: 'examine',
  created() {
    this.getLogin()
  },
  methods:{
    // 申请开户
    getLogin(msg) {
      let that = this;
      console.log(that.global.ws);
      that.global.ws.onmessage = function(res) {
        that.websocketonmessage (res)
      };
    },
    websocketonmessage (e) { // 数据接收
      this.redata = JSON.parse(e.data)
      console.log(this.redata,'开户审核')
      let _this = this
      if(this.redata.action === "checkResult"){
        if(this.redata.data.code === 0 && this.redata.action === "checkResult" && this.redata.data.object !== "-1"){
          localStorage.setItem('status','1')
          Toast({
            type:'success',
            message:_this.$t('openYes'),
            duration:1000,
            onClose(){
              _this.$router.push({
                name:'adopt',
              })
            }
          })
        }else {
          localStorage.setItem('status','-1')
          Toast({
            message:_this.$t('noPass'),
            duration:1000,
            onClose(){
              _this.$router.push({
                name:'notPass',
              })
            }
          })
        }
      }

      // let _this = this
      // if (this.redata.data.ok) {
      //   localStorage.setItem('Flag', 'isLogin')
      //   localStorage.setItem('account', _this.account)
      //   sessionStorage.setItem("disc",this.environment);
      //   this.$router.push({
      //     path:'home'
      //   })
      // } else {
      // }
    },
  }
}
</script>

<style scoped>

</style>



